/****************************************************************
 SPACING
****************************************************************/
/* Padding */
.content-pad-sm {
  padding-top: 3%;
  padding-bottom: 3%; }

.content-pad-md {
  padding-top: 6%;
  padding-bottom: 6%; }

.content-pad-lg {
  padding-top: 12%;
  padding-bottom: 12%; }

.content-pad-xlg {
  padding-top: 18%;
  padding-bottom: 18%; }

/****************************************************************
 ALIGNMENT
****************************************************************/
.alignnone {
  float: none;
  display: block;
  margin: 3rem auto; }

.alignleft {
  float: left;
  margin: 0 2rem 1rem 0; }

.alignright {
  float: right;
  margin: 0 0 1rem 2rem; }

.aligncenter {
  display: block;
  margin: 2rem auto !important; }

.alignfull {
  margin: 0 calc(50% - 50vw);
  max-width: 100vw;
  width: 100vw; }

.alignwide {
  margin: 0 calc(25% - 25vw); }

/****************************************************************
 WIDTH
****************************************************************/
.max-width-sm {
  max-width: 540px; }

.max-width-md {
  max-width: 720px; }

.max-width-lg {
  max-width: 960px; }

.max-width-xl {
  max-width: 1140px; }

.max-width-xxl {
  max-width: 1320px; }

.max-width-auto {
  max-width: 100%; }

a {
  transition: all 150ms ease-in-out; }

.navigation {
  justify-content: flex-end; }
  .navigation .navigation-menu li.nav-item:hover .dropdown-menu {
    display: block; }
    .navigation .navigation-menu li.nav-item:hover .dropdown-menu .dropdown-item:active,
    .navigation .navigation-menu li.nav-item:hover .dropdown-menu .dropdown-item:hover {
      background: #F5F5F5; }
  .navigation .navigation-menu li.nav-item .dropdown-item a {
    padding-top: .25rem;
    padding-bottom: .25rem;
    color: #212529; }
  @media screen and (max-width: 768px) {
    .navigation {
      overflow: auto; }
      .navigation .navigation-menu {
        min-height: 100vh; }
        .navigation .navigation-menu .dropdown-menu {
          display: block;
          border: 0; } }

.nav-open .navigation li.nav-item a {
  color: #212529; }
  .nav-open .navigation li.nav-item a:hover {
    color: #4e5862; }

.brand {
  position: relative;
  display: flex; }
  .brand .brand-logo {
    width: 100%;
    max-width: 200px; }
    .brand .brand-logo .st0 {
      fill: #F5F5F5; }
  .brand .brand-link {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.nav-open .brand .brand-logo .st0 {
  fill: #0d6efd; }

.navbar-toggler {
  padding: .25rem .5rem;
  margin-right: .5rem; }

.hamburger {
  width: 30px;
  height: 25px;
  margin: 3px;
  margin-top: 7px;
  position: relative;
  -webkit-transform: rotate(0);
  -moz-transform: rotate(0);
  -o-transform: rotate(0);
  transform: rotate(0);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer; }
  .hamburger span {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: #212529;
    border-radius: 9px;
    opacity: 1;
    right: 0;
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -o-transform: rotate(0);
    transform: rotate(0);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out; }
  .hamburger span:nth-child(1) {
    top: 0; }
  .hamburger span:nth-child(2),
  .hamburger span:nth-child(3) {
    top: 9px; }
  .hamburger span:nth-child(4) {
    top: 18px;
    width: 70%; }
  .hamburger.open span:nth-child(1) {
    top: 18px;
    width: 0%;
    right: 50%; }
  .hamburger.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg); }
  .hamburger.open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg); }
  .hamburger.open span:nth-child(4) {
    top: 9px;
    width: 0%;
    right: 50%; }

.nav-open .hamburger span {
  background: #212529; }

