.card {
  overflow: hidden;
  padding: 0;
  max-width: 100%;
  margin: 0;
  transition: all 200ms ease-in-out;
  margin-bottom: 1.25rem; }
  .card .card-image {
    position: relative;
    overflow: hidden;
    position: relative;
    padding-top: 54.25%;
    height: 100%; }
    .card .card-image img,
    .card .card-image a {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .card .card-image img {
      top: 50%;
      transform: translate(0, -50%);
      height: 100%; }
  .card .card-body {
    height: 100%; }
    .card .card-body .card-title a {
      color: inherit;
      text-decoration: none; }
      .card .card-body .card-title a:hover {
        text-decoration: revert; }
    .card .card-body .card-action {
      margin: 0; }
  .card .card-header,
  .card .card-footer {
    display: none; }
  .card.card-dark .card-body {
    display: block;
    background: #000000;
    color: #ffffff; }
  .card.card-dark .card-title {
    margin: .5rem .25rem; }
  .card.card-min .card-body > p,
  .card.card-min .card-body > .card-text,
  .card.card-min .card-body > .card-action {
    display: none; }

